define('ember-markdown-editor/index/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    html: '',
    markdown: '',

    actions: {
      updatePreview: function updatePreview(html) {
        this.set('html', html);
      }
    }
  });

});